//step 1
//imports
import React from 'react';
import ReactDOM from 'react-dom';
import faker from "faker";
import Comment from "./Comment"
import "./app.css"
import Approval from "./Approval"
//step 2
//
const App=()=>{
    return(<div className="App">

    <h1>Hello React World!!! This is simple comments app using Faker JS</h1>
        {/* HERE we want my comment component under my approval card */}
        {/* SO i am passing comment component under openning and closing tag of
            Approval in index.js
            and in approval component acces that data using props.children in Approval.js
        */} 
        <Approval>
            <Comment img={faker.image.avatar()} author={faker.name.firstName()} />
        </Approval>
        
        <Approval>
            <Comment img={faker.image.avatar()} author={faker.name.firstName()} />
        </Approval>

        <Approval>
            <Comment img={faker.image.avatar()} author={faker.name.firstName()} />
        </Approval>

        </div>);
}

//step 3
//Render Render accepts 2 args 1st is component and second is refrence to root

ReactDOM.render(
    <App />,
    document.querySelector("#root")
)